import { Component } from '@angular/core';
import { IonicPage, NavController, LoadingController, AlertController, ToastController, NavParams } from "ionic-angular";
import { UserProvider } from '../../providers/user/user';

@IonicPage({
  name: 'page-chat-detail',
  segment: 'chat'
})

@Component({
  selector: 'page-chat-detail',
  templateUrl: 'chat-detail.html'
})
export class ChatDetailPage {
  item: any;
  supportId: any;
  message: any;
  header: any;
  data: any;
  data1: any;
  memberid: any;
  username:any;
  UserImage:any;

  constructor(public user: UserProvider, public nav: NavController, public forgotCtrl: AlertController, public navparams: NavParams, public loadingCtrl: LoadingController, public toastCtrl: ToastController) {

    this.data1 = this.navparams.get('item');
    this.item = this.navparams.get('item').msg;
    this.supportId = JSON.parse(this.navparams.get('item').supportId);
    this.header = this.navparams.get('item').Header;
    console.log(this.item, this.supportId, this.header);

    this.memberid = JSON.parse(localStorage.getItem('MemberId'));
    var use = JSON.parse(localStorage.getItem('currentUser'));
    this.username = use.general.UserName;
    this.UserImage = use.UserImage;
    console.log(this.username, this.UserImage);

  }


  // when page leave
  ngOnDestroy() {
    console.log('leave');
    this.user.stateUpdate(this.supportId).subscribe(
      data => {
        console.log(data);
      }
    )

  }

  sendMessage() {
    console.log(this.message);
    this.user.addSupportSendId(this.header, this.message, this.supportId).subscribe(
      data => {
        console.log(data);
        this.data = JSON.parse(JSON.stringify(data));
        this.item = JSON.parse(this.data.data.Text);
        console.log(this.item);
        this.message = null;
      }, error => {
        console.log(error.JSON);
        setTimeout(() => {
          this.nav.setRoot('page-general_error');
        }, 1000);
      },
    )
  }



}



